* {
    margin: 0;
    padding: 0;
}
body {
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.todo-form {
    height: 520px;
    width: 25rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    border: none;
    border-radius: 0 2rem;
    box-shadow: rgba(0, 0, 0, 0.15 ) 0px 4px 4px 0px, rgba(0, 0, 0, 0.15) 8px 32px 32px 0px, rgba(0, 0, 0, 0.1) 8px 64px 64px 0px ;
}
.heading{
    margin-top: 1rem;
    margin-bottom: 1.5rem;
    font-size: 2rem;
    color: #3b3b98;
}
.todo-input{
    height: 3rem;
    width: 18rem;
    margin: 8px;
    padding-left: 1rem;
    border: none;
    border-radius: 4px;
    font-size: large;
    box-shadow: rgba(0, 0, 0, 0.1 ) 0px 4px 6px -1px, rgba(0, 0, 0, 0.06) 0px 2px 4px -1px ;
}
textarea[type="message"]{
    font-size: 1.3rem;
    resize: none;
    height: 9rem;
}
textarea[type="message"]::placeholder{
    letter-spacing: -0.2rem;
}

.todo-button{
    height: 3rem;
    width: 18rem;
    margin: 2rem;
    border-radius: 3px;
    border: none;
    font-size: 1rem;
    background-color: #7438e3;
    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px 0px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2 ) 0px -3px 0px inset ;
}

.todo-button:hover{
    box-shadow: rgba(0, 0, 0, 0.1 ) 0px 1px 3px 0px, rgba(0, 0, 0, 0.06) 0px 1px 2px 0px ;
}